import React from '../index'
import Banner from './components/Banner'
import Nav from './components/Nav'
import Content from './components/Content'
import Demo from './components/Demo'
import Footer from './components/Footer'
import './App.less'

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      fix: false,
    }
  }
  componentDidMount() {
    window.addEventListener('scroll', e => {
      if (window.pageYOffset > 400 && !this.state.fix) {
        this.setState({ fix: true })
      } else if (window.pageYOffset <= 400 && this.state.fix) {
        this.setState({ fix: false })
      }
    })
  }
  render() {
    return (
      <div className="App">
        <Banner/>
        <Nav fix={this.state.fix}>
          <a className={{ tab: true, color: this.state.fix }} href="#intro"></a>
          <a className={{ tab: true, color: this.state.fix }} href="#demo"></a>
          <a className={{ tab: true, color: this.state.fix }} href="#docs"></a>
        </Nav>
        <Content fix={this.state.fix}>
          <div id="intro" className="anchor" />
          <div className="item">
            <h1 className="title"></h1>
            <p className="desc indent">KutReact-LikeReactKutcreateElementrenderComponentKut</p>
            <p className="desc indent">ReactKutReactKutdiffReactdiffdiff<a href="https://zhuanlan.zhihu.com/p/20346379"></a>ReactdiffKutdiffdiffdiff</p>
            <p className="desc indent">ReactFiberContextPortalPull RequestStar<a href="https://github.com/facebook/react">React</a><a href="https://github.com/CodeFalling/react-tiny">React-Tiny</a><a href="https://github.com/RubyLouvre/anu">Anu</a><a href="https://github.com/215566435/Luy">Luy</a><a href="http://www.iconfont.cn">Iconfont</a></p>
          </div>
          <div id="demo" className="anchor" />
          <div className="item">
            <h1 className="title"></h1>
            <Demo />
          </div>
          <div id="docs" className="anchor" />
          <div className="item">
            <h1 className="title"></h1>
            <p className="desc indent">KutTypeScriptVSCodeReact</p>
            <h2 className="title">Kut.createElement</h2>
            <section className="code">function createElement(type, config, ...children): KutElement</section>
            <p className="desc indent">KutElementtypeconfigchildrentypestringtypeof Componentconfiganychildrenstring[]number[]KutElement[]KutElementVirtual DOMtypeDOMdivh1Kut.ComponentconfigVirtual DOMkeyclassNamestylechildrenKutElementJSXBabel</p>
            <h2 className="title">Kut.Component</h2>
            <section className="code">class Component {'{'} constructor(props) {'{'} {'}'} {'}'}</section>
            <p className="desc indent">ReactContextReact-ReduxpropsVirtual DOMsetState(state)forceUpdate()componentWillMount()componentDidMount()shouldComponentUpdate(nextProps, nextState)componentWillUpdate(nextProps, nextState)componentDidUpdate()componentWillUnmount()</p>
            <h2 className="title">Kut.render</h2>
            <section className="code">function render(element, container?): void | string</section>
            <p className="desc indent">elementcontainerelementstringnumberKutElementcontainerHTMLElementDOMcontainercontainer.innerHTML</p>
          </div>
        </Content>
        <Footer />
      </div>
    )
  }
}

export default App
